"use client";
import useSWR from 'swr';
interface SpotifyData {
    isPlaying: boolean;
    title?: string;
    album?: string;
    artist?: string;
    albumImageUrl?: string;
    songUrl?: string;
};
const fetcher = (url: string) => fetch(url).then((r) => r.json());


export default function DashBoard() {
const { data, error } = useSWR<SpotifyData>('/api/spotify', fetcher);

if (error) {
    return <div>Failed to load Spotify data.</div>;
}

if (!data) {
    return <div>Loading...</div>;
}

return (
<main className="justify-center flex items-center">

<div className='size-64 bg-amber-300'></div>
<div>
{data.isPlaying ? (
        <div className="text-center text-pink-400">
        <h1 className="text-2xl font-bold">Now Playing: {data.title}</h1>
        <p className="text-lg">Album: {data.album}</p>
        <p className="text-lg">Artist: {data.artist}</p>
        <img
            src={data.albumImageUrl}
            alt={data.album}
            className="mx-auto my-4 rounded-lg"
        />
        <a
            href={data.songUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-500 underline"
        >
            Listen on Spotify
        </a>
        </div>
    ) : (
        <p className="text-lg">No track is currently playing.</p>
    )}
</div>


</main>
);
}